.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
@media (max-width: 1700px){
  font-size: 75%;
}

}

body{
background: #1b1b1b;
font-family: 'Inter', sans-serif;
overflow-x: hidden;
}

button{
font-weight: bold;
font-size: 1.1.rem;
cursor: pointer;
padding: 1rem 2rem;
border: 3px solid #23d997;
background: transparent;
color: white;
transition: all 0.5s ease;
font-family: 'Inter', sans-serif;
&:hover{
  background-color: #23d997;
  color: white;
}
}

h2{
  font-weight: lighter;
  font-size: 4rem;
}
h3{
  color: white;
}
h4{
  font-weight: bold;  
  font-size: 2rem;
}
a{
  font-size: 1.1rem;
}
span{
  font-weight: bold;
  color: #23d997;        
}
p{
  padding: 3rem 0rem;
  color: #ccc;
  font-size: 1.4rem;
  line-height: 150%;
}

.description {
  flex: 1;
  padding-right: 5rem;
  z-index: 2;
  h2 {
    font-weight: lighter;
  }
  @media (max-width: 1300px) {
    padding: 0;
    button {
      margin: 2rem 0rem 5rem 0rem;
    }
  }
}

.about {
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem 10rem;
  color: white;
  @media (max-width: 1300px) {
    display: block;
    padding: 2rem 2rem;
    text-align: center;
}

.image {
  z-index: 2;
  flex: 1;
  overflow: hidden;
  border-radius: 25px;
  img {
    width: 100%;
    height: 80vh;
    object-fit: cover;
}
}
}