.nav-container {
min-height: 10vh;
display: flex;
margin: auto;
justify-content: space-between;
align-items: center;
padding: 1rem 8rem;
background: #282828;
position: sticky;
top: 0;
z-index: 10;
.link {
    font-size: 1.5rem;
  color: white;
  text-decoration: none;
}
ul {
  display: flex;
  list-style: none;
}
#logo {
  font-size: 4rem;
  font-family: "Lobster", cursive;
  font-weight: lighter;
  color: orangered;
  text-decoration: none;
}
li {
  padding-left: 10rem;
  position: relative;
}
@media (max-width: 1300px) {
  flex-direction: column;
  padding: 2rem 1rem;
  #logo {
    display: inline-block;
    margin: 1rem;
  }
  ul {
    padding: 2rem;
    justify-content: space-around;
    width: 100%;
    li {
      padding: 0;
    }
  }
}

.line {
    height: 0.3rem;
  background: orangered;
  width: 0%;
  position: absolute;
  bottom: -80%;
  left: 60%;
  @media (max-width: 1300px) {
    left: 0%;
  }
}

};
